export default {
	data() {
		return {
			isLoaing: true,
			imgUrl: this.imgUrl,
			categorys: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true
		};
	},
	onLoad() {

	},
	onShow() {
		this.getGoodsList()
	},
	methods: {
		//跳转搜索页面
		toSearch() {
			uni.navigateTo({
				url: "../search/search"
			})
		},
		getGoodsList() {
			uni.request({
				url: this.websiteUrl + "/api/Goods/categoryList",
				method: "POST",
				success: (res) => {
					console.log(res.data)
					this.isLoaing = false
					if (res.data.status == 0) {
						this.categorys = res.data.data
						return
					}
					this.showToast(res.data.msg)
				},
				fail: () => {
					this.showToast('系统繁忙，请稍后重试！')
				}
			})
		},
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			if (this.load) {
				for (let i = 0; i < this.categorys.length; i++) {
					let view = uni.createSelectorQuery().select("#main-" + i);
					view.fields({
						size: true
					}, data => {
						this.categorys[i].top = tabHeight;
						tabHeight = tabHeight + data.height;
						this.categorys[i].bottom = tabHeight;
					}).exec();
				}
				this.load = false
			}
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.categorys.length; i++) {
				if (scrollTop > this.categorys[i].top && scrollTop < this.categorys[i].bottom) {
					this.verticalNavTop = i * 50
					this.tabCur = i
					// console.log(scrollTop)
					return false
				}
			}
		},
		toGoodsList(id) {
			uni.navigateTo({
				url: '../searchDetail/searchDetail?cat_id=' + id
			})
		}
	},
}
